body, html {
  margin: 0;
  padding: 0;
  font-family: 'Inter', sans-serif;
  color: #f1f5f9;
  min-height: 100vh;
}

.dark-theme {
  display: flex;
  flex-direction: column;
  min-height: 100vh;
  background-color: #0f172a;
}

.header {
  text-align: center;
  padding: 2rem 1rem 0.5rem;
}

.header h1 {
  font-size: 2.4rem;
  color: #ffffff;
  margin-bottom: 0.5rem;
}

.header p {
  color: #94a3b8;
  margin: 0;
}

.main-content {
  flex-grow: 1;
  display: flex;
  justify-content: center;
  align-items: start;
  padding: 2rem;
}

.question-card, .result-card {
  background: #1e293b;
  border-radius: 1.2rem;
  box-shadow: 0 12px 30px rgba(0,0,0,0.3);
  padding: 2rem;
  width: 100%;
  max-width: 620px;
  text-align: center;
  animation: fadeSlideUp 0.5s ease;
}

.step-info {
  font-size: 0.9rem;
  color: #cbd5e1;
  margin-bottom: 1rem;
}

.question-card h2,
.result-card h2 {
  font-size: 1.5rem;
  color: #ffffff;
  margin: 0.5rem 0;
}

.description {
  margin-bottom: 1.5rem;
  color: #94a3b8;
}

select, input {
  width: 100%;
  padding: 0.7rem;
  font-size: 1rem;
  margin-bottom: 1.5rem;
  border: none;
  border-radius: 0.5rem;
  background: #334155;
  color: #f8fafc;
}

select:focus, input:focus {
  outline: 2px solid #38bdf8;
}

.button-group {
  display: flex;
  justify-content: center;
  gap: 1rem;
  flex-wrap: wrap;
}

button {
  padding: 0.6rem 1.2rem;
  font-size: 1rem;
  border: none;
  border-radius: 0.5rem;
  background: #38bdf8;
  color: #0f172a;
  font-weight: 500;
  transition: all 0.2s ease-in-out;
  cursor: pointer;
}

button:hover {
  transform: scale(1.05);
  background: #0ea5e9;
}

button:disabled {
  background: #475569;
  color: #cbd5e1;
  cursor: not-allowed;
}

.loading {
  margin-top: 1rem;
  color: #38bdf8;
  font-weight: 500;
  animation: pulse 1.2s infinite;
}

.rate-note {
  font-size: 0.9rem;
  color: #cbd5e1;
}

.footer {
  text-align: center;
  padding: 1.25rem;
  background: #1e293b;
  border-top: 1px solid #334155;
}

.footer a {
  background: #334155;
  color: #f1f5f9;
  padding: 0.6rem 1.25rem;
  border-radius: 0.5rem;
  text-decoration: none;
  font-weight: 500;
  transition: background 0.3s ease;
}

.footer a:hover {
  background: #475569;
}

@keyframes pulse {
  0% { opacity: 0.5; }
  50% { opacity: 1; }
  100% { opacity: 0.5; }
}

@keyframes fadeSlideUp {
  0% {
    opacity: 0;
    transform: translateY(20px);
  }
  100% {
    opacity: 1;
    transform: translateY(0);
  }
}

.progress-wrapper {
  width: 100%;
  margin-bottom: 1rem;
}

.progress-bar-container {
  background-color: #e0e0e0;
  border-radius: 8px;
  height: 10px;
  overflow: hidden;
}

.progress-bar {
  height: 100%;
  background-color: #007bff;
  transition: width 0.4s ease;
}

.progress-text {
  font-size: 0.75rem;
  color: #6b7280;
  text-align: right;
  margin-top: 4px;
}
